import React, { PureComponent } from 'react';
import { Link as ScrollLink } from 'react-scroll';
import pages from '../pages.json';

import Box from '../../components/Box';
import Text from '../../components/Text';
import Flex from '../../components/Flex';

class Option extends PureComponent {

  render() {
    const { isMobile, nowPage, onSetActive, ...props } = this.props;
    return (
      <Flex flexDirection={['column', null, 'row']} {...props}>
        {pages.map((page, index) => {
          const active = nowPage === page.to;
          return (
            <Flex key={index} mb={['0.5em', null, '0']} justifyContent={['center', null, 'flex-start']}>
              <ScrollLink
                smooth
                spy
                to={page.to}
                offset={-80}
                onSetActive={() => onSetActive(page.to)}
                style={{ cursor: 'pointer' }}
              >
                <Box
                  color={active ? 'black' : 'gray'}
                  borderBottom="3px solid"
                  borderColor={active ? 'yellow' : 'transparent'}
                  pb="0.25em"
                  fontWeight={active ? 'bold' : 'normal'}
                  fontSize={['0.8em', null, '1em']}
                >
                  {page.label}
                </Box>
              </ScrollLink>
            {!isMobile && index < pages.length - 1 && (<Text color="gray" mx="0.5em" lineHeight="1">|</Text>)}
            </Flex>
          );
        })}
      </Flex>
    );
  }
}

export default Option;
